 @import "../../../css-config/mixins.scss";

 .navbar {
     overflow: hidden;
     background-color: #333;
    
    //  margin: 20px 0;
     display: none;

     @include md{
       
        display: flex;
        justify-content: center;
     }
   
    
   
     




     .dropdown {
         float: left;
         overflow: hidden;


         .dropbtn {
             font-size: 16px;
             border: none;
             outline: none;
             color: white;
             padding: 14px 23px;
             background-color: inherit;
             font-family: inherit;
             margin: 0;
             display: flex;
             justify-content: center;
             align-items: center;
             gap: 10px;
         }

        
         &:hover .dropbtn {
             background-color: rgb(15, 255, 91);
             color: black;
             font-weight: 500;
         }

         .dropdown-content {
             display: none;
             position: absolute;
             background-color: #f9f9f9;
             min-width: 160px;
             box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
             z-index:99;
           
             

             a {
                float: none;
                color: black;
                padding: 12px 16px;
                text-decoration: none;
                display: block;
                text-align: left;
                display: flex;
                // justify-content: center;
                align-items: center;
                gap: 20px;
                cursor: default;
                border-bottom: 1px solid rgba(0,0,0,0.5);
                font-weight: 600;
                
                

                &:hover {
                    background-color: #ddd;
                }
            }
         }

         

         

         &:hover .dropdown-content {
             display: block;
        
         }
     }
 }